<template>
  <div class="login">
    <!-- <el-button type="text" @click="centerDialogVisible = true">点击打开 Dialog</el-button>

<el-dialog
  title="提示"
  :visible.sync="centerDialogVisible"
  width="30%"
  center>
  <span>需要注意的是内容是默认不居中的</span>
  <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
  </span>
</el-dialog> -->
    <img src="../assets/icon.png" alt="logo" class="logo">
    <el-row>
        <el-col :span="6" :offset="9">
            <div class="grid-content bg-purple-light tologin">
                <div class="top">
                    <img src="http://img2.imgtn.bdimg.com/it/u=2708765208,3684039786&fm=26&gp=0.jpg" alt="logo" class="head">
                    <h4>Welcome to NoJs</h4>
                </div>
                <el-form v-if="!forget" style="margin-top:25px;" :model="form">
                    <el-form-item>
                        <el-input v-model="form.name" placeholder="用户"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-input v-model="form.region" placeholder="密码" type="password"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="success" style="width:100%;" @click="submitForm('ruleForm')">登录</el-button>
                    </el-form-item>
                </el-form>

                <el-form v-if="forget" style="margin-top:25px;" :model="formForget">
                    <el-form-item>
                        <el-input v-model="formForget.name" placeholder="用户名"></el-input>
                    </el-form-item>
                    <el-form-item class="emailcode">
                        <el-input placeholder="邮箱地址" v-model="formForget.email">
                            <template slot="append"><el-button type="text" style="margin:0px 1px; width:35px; text-algin:center;" @click="verification" :disabled="btn">{{vercode}}</el-button></template>
                        </el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-input v-model="formForget.code" placeholder="验证码"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-input v-model="formForget.password" placeholder="新密码"  type="password"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" style="width:100%;" @click="submitForm('ruleForm')">找回密码</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div class="grid-content bg-purple-light forget">
                <p>{{forget?tips[1].text:tips[0].text}} <el-button type="text" @click="forget = !forget"> {{forget?tips[1].btn:tips[0].btn}}</el-button></p>
            </div>
        </el-col>
    </el-row>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'home',
  
  data() {
      return {
        centerDialogVisible: false,
        form: {
          name: '',
          region: '',
          type: '',
          email: ''
        },
        formForget: {
          name: '',
          code: '',
          password: '',
          email: ''
        },
        vercode: '验证码',
        forget: false,
        btn: false,
        tips:[{
            text:'忘记了密码？',
            btn:'找回密码'
        },{
            text:'想起密码了！',
            btn:'返回'
        }]
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      verification() {
          if(this.btn == false && this.form.email != ''){
              this.btn = true;
              this.vercode='60s'
              let times = 60;
              let timer = setInterval(()=>{
                  times --;
                  this.vercode= times + 's';
                  if(times <= 50){
                      this.vercode = '验证码';
                      this.btn = false;
                      clearInterval(timer);
                  }
              }, 1000);
          }
      }
    }
}
</script>

<style lang="scss" scoped>
.login{
    margin: 0;
    padding: 0;
}
.logo{
    width: 100px;
    height: 100px;
    margin: 25px auto;
}
.tologin{
    min-height: 50vh;
    width: 85%;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 20px;
    .top{
        border-bottom: #e5e9f2 solid 1px;
        padding-bottom: 20px;
        .head{
            width: 50px;
            height: 50px;
            border-radius: 50%;
            box-shadow: 0px 0px 2px rgba($color: #000000, $alpha: 0.1);
        }
    }
    
}
.forget{
    width: 85%;
    margin: 20px auto;
    margin-bottom: 0px;
    height: 40px;
    p{
        margin-top: 0px;
        font-size: 12px;
         button{
            
            font-size: 12px;
            color: #409EFF;
        }
    }
}
.el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: rgba($color: #000000, $alpha: 0.01);
    border: 1px solid #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>

